import router from './routers'
import store from '@/store'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import { getToken } from '@/utils/auth' // getToken from cookie
import { platformlogin, getShopInfoById } from '@/api/administrator'
import { setToken } from '@/utils/auth'

NProgress.configure({ showSpinner: false }) // NProgress Configuration

const whiteList = ['/login'] // no redirect whitelist

router.beforeEach((to, from, next) => {
  NProgress.start()
  let { userId } = store.getters;
  if (to.meta.title) {
    document.title = to.meta.title
  }
  if (!userId) store.commit('SET_USER_ID', localStorage.getItem('userId'))
  store.commit('SET_MENU_ACTIVE', to.path)
  next()
  if (to.query.administratorInfo) {
    // 店铺代管理
    let administratorInfo = to.query.administratorInfo
    console.log(administratorInfo, 'administratorInfo')
    administratorInfo && (() => { // 店铺代管理
      let cellPhone = administratorInfo.split('-')[0]
      let shopId = administratorInfo.split('-')[1]
      let data = {
        cellPhone,
        shopId
      }
      platformlogin(data).then(res => {
        console.log('店铺代管理登录', res)
        if (res.code == 200) {
          store.commit('SET_USER_ID', res.data.id)
          localStorage.setItem('userId', res.data.id)
          localStorage.setItem('userName', res.data.userName)
          localStorage.setItem('shopId', res.data.shopId)
          setToken(res.data.token, false)
          store.commit('SET_TOKEN', res.data.token)
          store.commit('userType', res.data.type)
          let data = {
            shopId
            // userId: res.data.id
          }
          getShopInfoById(data).then(res => {
            if (res.code == 200) {
              console.log('获取店铺详情', res)
              store.commit('SET_SHOP_ID', res.data.id)
              store.commit('SET_SHOP_NAME', res.data.shopName)
              store.commit('SET_SHOP_LOGO', res.data.logoUrl)
              store.dispatch('loadMenus').then(() => {
                next('/shopIndex')
                NProgress.done()
              })
            }
          })
        }
      })
    })()
  }
  if (getToken()) {
    if (to.path === '/login') {
      next({ path: '/selectShop' })
      NProgress.done()
    } else {
      if (!to.meta.noPer) {
        store.dispatch('loadMenus').then(() => {
          // store.dispatch('loadPermission').then(()=>{
          next()
          NProgress.done()
          // })
        })
      } else {
        store.commit('SET_LOAD_MENUS', true)
        next()
      }
    }
  } else {
    /* has no token*/
    if (whiteList.indexOf(to.path) !== -1) {
      // 在免登录白名单，直接进入
      next()
    } else {
      // next(`/login?redirect=${to.path}`) // 否则全部重定向到登录页
      next('/login')
      NProgress.done()
    }
  }
})
router.afterEach(() => {
  NProgress.done() // finish progress bar
})
