import { constantRouterMap } from '@/router/routers'
import { info } from '@/api/system/permission'
import { getToken } from '@/utils/auth'
import Layout from '@/layout/Layout'

const permission = {
  state: {
    routers: constantRouterMap,
    addRouters: [],
    getPermission:true
  },
  mutations: {
    SET_ROUTERS: (state, routers) => {
      state.addRouters = routers
      state.routers = constantRouterMap.concat(routers)
    },
    SET_PERMISSION_STATUS: (state, b) => {
      state.getPermission = b
    },
  },
  actions: {
    GenerateRoutes({ commit }, asyncRouter) {
      commit('SET_ROUTERS', asyncRouter)
    },
    // 获取权限
    loadPermission({ commit , dispatch , getters}){
      commit('SET_PERMISSION_STATUS', false)
      info({
        token: getToken(),
        userId:getters.userId || localStorage.getItem('shopId'),
        shopId:getters.shopId || localStorage.getItem('shopId')
      }).then(res => {
        console.log('获取权限',res)
        commit('SET_ROLES', res.prims)
        commit('SET_PERMISSION_STATUS', true)
      })
    }
  }
}

export const filterAsyncRouter = routers => {
  // 遍历后台传来的路由字符串，转换为组件对象
  const accessedRouters = routers.filter(router => {
    if (router.component) {
      if (router.component === 'Layout') {
        // Layout组件特殊处理
        router.component = Layout
      } else {
        const component = router.component
        router.component = loadView(component)
      }
    }
    if (router.children && router.children.length) {
      router.children = filterAsyncRouter(router.children)
    }
    return true
  })
  return accessedRouters
}

export const loadView = view => {
  // 路由懒加载
  return () => import(`@/views/${view}`)
}

export default permission
