<template>
  <div class="navbar flex-row-start">
    <hamburger :toggle-click="toggleSideBar" :is-active="sidebar.opened" class="hamburger-container" />
    <div class="navBox flex-row-between">
      <div>您好！{{user.userName}} 欢迎访问系统应用！</div>
      <div class="flex-row-start">
        <div class="flex-row-start">
          <el-popover
            placement="bottom"
            width="400"
            trigger="click">
            <div class="imgCode">
              <el-tabs tab-position="left">
                <el-tab-pane label="H5">
                  <img style="width:100px;height:100px;" :src="form.shopIndexImgUrl" alt="">
                </el-tab-pane>
                <el-tab-pane :disabled="form.shopMiniStatus == 2" label="小程序">
                  <img style="width:100px;height:100px;" :src="form.miniIndexImgUrl" alt="">
                </el-tab-pane>
              </el-tabs>
            </div>
            <el-button slot="reference">访问店铺</el-button>
          </el-popover>
        </div>
        <div class="curP flex-row-start" style="margin-left:100px;">
          <div>
            <img class="shopLogo" v-if="shop.shopLogo" :src="shop.shopLogo" alt="">
          </div>
          <el-dropdown @command="handleCommand">
            <span class="el-dropdown-link">
              <span>{{shop.shopName}}</span>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-if="userType != 2" icon="el-icon-set-up" command="a">切换店铺</el-dropdown-item>
              <el-dropdown-item icon="el-icon-switch-button" command="b">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters ,mapState } from 'vuex'
import Hamburger from '@/components/Hamburger'
import { getShopQrCode } from '@/api/system/mpCode'


export default {
  components: {
    Hamburger,
  },
  data () {
    return {
      form:{}
    }
  },
  computed: {
    ...mapGetters([
      'sidebar',
      'user',
      'device'
    ]),
    ...mapState([
      'user',
      'shop'
    ]),
    userType() {
      return this.$store.state.userType
    }
  },
  created(){
    this.getCode()
  },
  methods: {
    getCode(){
      getShopQrCode(this.shop.shopId).then(res=>{
        console.log('获取店铺二维码',res)
        if(res.code == 200){
          this.form = res.data
        }
      })
    },
    toggleSideBar () {
      this.$store.dispatch('ToggleSideBar')
    },
    // 下拉菜单的操作
    handleCommand(command) {
      if(command == 'a'){
        this.$confirm('确定要跳转至选择店铺吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push({path:'/selectShop'})
        }).catch(() => {});
      }
      if(command == 'b'){
        this.$confirm('确定要退出系统吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '登出成功!'
        });
        this.$store.dispatch('LogOut').then(() => {
          location.reload() // 为了重新实例化vue-router对象 避免bug
        })
      }).catch(() => {});
      }
    }
  },
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.imgCode /deep/ .el-tabs__nav-scroll{
  border-bottom:none;
}
.navbar {
  width: 100%;
  height: 50px;
  line-height: 50px;
  border-radius: 0px !important;
  .hamburger-container {
    line-height: 58px;
    height: 50px;
    float: left;
    padding: 0 10px;
  }
  .navBox{
    box-sizing: border-box;
    width: 100%;
    .shopLogo{
      width: 32px;
      height: 32px;
      vertical-align: top;
      margin-right: 10px;
    }
  }
  .dropdown{
    width: 32px;
    height: 32px;
    vertical-align: top;
  }
}
</style>
