import Vue from 'vue'

import 'normalize.css/normalize.css' // A modern alternative to CSS resets
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import locale from 'element-ui/lib/locale/lang/zh-CN' // lang i18n
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import '@/styles/index.scss' // global css
import App from './App'
import router from './router/routers'
import permission from './components/permission'
import store from './store'
import '@/icons' // icon
import './router/index' // permission control
import md5 from 'js-md5'
import Lodash from 'lodash'
import axios from 'axios'
import VueAMap from 'vue-amap';
import SlideVerify from 'vue-monoplasty-slide-verify'
Vue.prototype._ = Lodash
import '../static/ueditor1_4_3_3/ueditor.config'
import '../static/ueditor1_4_3_3/ueditor.all'
import '../static/ueditor1_4_3_3/ueditor.parse.min'
import '../static/ueditor1_4_3_3/lang/zh-cn/zh-cn'
import clipboard from 'clipboard'
Vue.use(VueAMap);
Vue.use(SlideVerify)
Vue.prototype.clipboard = clipboard
Vue.prototype.$md5 = md5
Vue.use(mavonEditor)
Vue.use(permission)
Vue.use(ElementUI, { locale })
Vue.config.productionTip = false
Vue.prototype.axios = axios
import VueDND from 'awe-dnd'
VueAMap.initAMapApiLoader({
  key: '583e4b4e4d3e1d1c11ce5cf6c706282c',
  plugin: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType','Geocoder','Geolocation','SimpleMarker'],
  v: '1.4.4'
});
Vue.use(VueDND)
require('babel-polyfill')
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})
