import request from '@/utils/request'
// 获取店铺首页二维码
export function getShopQrCode(id) {
  return request({
    url: `/sys/shopCensus/getShopQrCode?shopId=${id}`,
    method: 'get',
  })
}
// 获取店铺微页面首页二维码
export function getShopVpageQrCode(id,vid) {
  return request({
    url: `/sys/shopCensus/getShopVpageQrCode?shopId=${id}&vpageId=${vid}`,
    method: 'get',
  })
}
