/**
 * @description 系统全局配置
 */
import apiModules from './api/modules'
import apiDialogPicture from './api/dialogPicture'
import apiDialogUrl from './api/dialogUrl'
import apiDialogGoods from './api/dialogGoods'
import apiPageList from './api/pageList'

import localConfig from './local/config'

export {
  apiModules,
  apiDialogPicture,
  apiDialogUrl,
  apiDialogGoods,
  apiPageList,
  localConfig
}
export default {
  /**
   * @description 记住密码状态下的token在Cookie中存储的天数，默认1天
   */
  tokenCookieExpires: 1,
  /**
   * @description 记住密码状态下的密码在Cookie中存储的天数，默认1天
   */
  passCookieExpires: 1,
  /**
   * @description 此处修改网站名称
   */
  webName: '',
  /**
   * @description 是否只保持一个子菜单的展开
   */
  uniqueOpened: true,
  /**
   * @description token key
   */
  TokenKey: 'ADMIN-TOEKN',

  /**
   * @description 请求超时时间，毫秒（默认2分钟）
   */
  timeout: 1200000,

  /**
   * @description 是否显示 tagsView
   */
  tagsView: true,

  /**
   * @description 固定头部
   */
  fixedHeader: true,

  /**
   * @description 是否显示logo
   */
  sidebarLogo: true,

  /**
   * 是否显示设置的悬浮按钮
   */
  settingBtn: true,

  /**
   * 是否显示设置的底部信息
   */
  showFooter: true,

  /**
   * 底部文字，支持html语法
   */
  footerTxt: '@2019 泉联通 All right reserved.',

  /**
   * 备案号
   */
  caseNumber: '京ICP备18008931号-3'
}
