import { buildMenus } from '@/api/system/menu'
import { filterAsyncRouter } from '@/store/modules/permission'
import { getToken } from '@/utils/auth'
import router from '../../router/routers'
const shop = {
  state: {
    shopId: 0,                  // 店铺id
    shopName:'',                // 店铺名称
    shopLogo:'',                // 店铺头像
    // 第一次加载菜单时用到
    loadMenus: true,
  },

  mutations: {
    SET_SHOP_ID: (state, id) => {
      state.shopId = id
      localStorage.setItem('shopId',id)
    },
    SET_SHOP_NAME: (state, id) => {
      state.shopName = id
      localStorage.setItem('shopName',id)
    },
    SET_SHOP_LOGO: (state, id) => {
      state.shopLogo = id
      localStorage.setItem('shopLogo',id)
    },
    SET_LOAD_MENUS: (state, loadMenus) => {
      state.loadMenus = loadMenus
    },
  },
  actions: {
    // 获取目录树
    loadMenus({ commit , dispatch , getters}){
      commit('SET_LOAD_MENUS', false)
      buildMenus({
        token: getToken(),
        userId:getters.userId || localStorage.getItem('shopId'),
        shopId:getters.shopId || localStorage.getItem('shopId')
      }).then(res => {
        const asyncRouter = filterAsyncRouter(res)
        console.log('获取目录树',res)
        asyncRouter.push({ path: '*', redirect: '/404', hidden: true })
        dispatch('GenerateRoutes', asyncRouter).then(() => {
          // 存储路由
          router.addRoutes(asyncRouter) // 动态添加可访问路由表
        })
        commit('SET_LOAD_MENUS', true)
      })
    }
  }
}
export default shop
